html, body {
    height: 100vh;
}

.root {
    height: 100%;
}


/* Sidepanel */

.main-sidepanel {
    width: 320px;
    height: 100%;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #2A2E38;
    background: linear-gradient(rgba(32, 36, 47, 0.3), rgba(32, 36, 47, 0.3)), linear-gradient(rgba(13, 17, 29, 1), rgba(13, 17, 29, 1));
}

.main-sidepanel-header {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 24px;
    border-bottom: 1px solid #2A2E38;
}

.main-sidepanel-header-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 11px;
    cursor: pointer;
}

.main-sidepanel-header-title-logo {
    width: 28px;
    height: 28px;
}

.main-sidepanel-header-title-text {
    font-size: 23px;
    font-weight: 500;
    color: white;
}

.main-sidepanel-content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 24px;
}

.main-sidepanel-navigation {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.main-sidepanel-navigation-item {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 12px 16px;
    opacity: 50%;
    cursor: pointer;
}

.main-sidepanel-navigation-item-text {
    font-size: 16px;
    color: white;
}


/* Page */

.cases-page, .case-page, .libraries-page, .library-page {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.cases-main, .case-main, .libraries-main, .library-main {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 40px;
    background: #0D111D;
}


/* List view */

.list {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 32px;
}

.list-header {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 16px;
    align-items: center;
}

.list-header-title {
    width: 100%;
}

.list-header-title-text {
    font-size: 32px;
    color: white;
    cursor: default;
}

.list-header-button {
    white-space: nowrap;
    align-items: center;
    background: #4000FF;
    border-radius: 8px;
    padding: 12px 20px;
}

.list-header-button-text {
    font-size: 16px;
    font-weight: 500;
    color: white;
}

.list-table {
    width: 100%;
    border-collapse: collapse;
    thead {
        font-size: 14px;
        font-weight: 500;
        color: rgba(255, 255, 255, 0.6);
        background: #171B26;
        border-radius: 8px;
        th {
            padding: 12px;
        }
    }
    tbody {
        font-size: 16px;
        color: white;
        td {
            padding: 16px 12px;
            border-bottom: 1px solid #272D40;
        }
    }
}

.list-row-delete {
    display: flex;
    justify-content: end;
}

